.afterLogin{
	display: block;
	width: 100%;
	margin-top: .266667rem;
	background: #fff;
	padding: .373333rem 0 0 .4rem;
	.afterLogin_body{
		display: -ms-flexbox;
		display: flex;
		.afterLogin_body_sub{
			-ms-flex: 1 1;
			    flex: 1 1;
			.shop_again{
				display: -ms-flexbox;
				display: flex;
				border-top: 1PX solid #eee;
				padding: .266667rem .426667rem;
				-ms-flex-pack: end;
				    justify-content: flex-end;
				button{
					padding: .133333rem .266667rem;
					border:1PX solid #2395ff;
					border-radius: .053333rem;
					background: transparent;
					outline: none;
					font-size: .346667rem;
					color: #2395ff;
					margin-left: .2rem; 
				}
			}
			.afterLogin_dsc{
				display: -ms-flexbox;
				display: flex;
				-ms-flex-pack: justify;
				    justify-content: space-between;
				padding: .4rem .346667rem .4rem 0;
				.afterLogin_dsc_price{
					-ms-flex-preferred-size: 1.6rem;
					    flex-basis: 1.6rem;
					text-align: right;
					font-size: .346667rem;
					color: #333;
					font-weight: 700;
				}
				.afterLogin_dsc_foods{
					font-size: .346667rem;
					color: #666;
					display: -ms-flexbox;
					display: flex;
					-ms-flex-align: center;
					    align-items: center;
					span:first-child{
						max-width: 4.266667rem;
						display: inline-block;
						overflow: hidden;
						-o-text-overflow: ellipsis;
						   text-overflow: ellipsis;
						white-space: nowrap;
					}
					span:last-child{
						font-size: .346667rem;
						color: #666;
					}
				}
			}
			.afterLogin_body_sub_head{
				border-bottom: 1PX solid #eee;
				padding-right: .346667rem;
				padding-bottom: .266667rem;
				.afterLogin_time{
					font-size: .293333rem;
					color: #999;
				}
				.afterLogin_title{
					display: -ms-flexbox;
					display: flex;
					-ms-flex-align: center;
					    align-items: center;
					-ms-flex-pack: justify;
					    justify-content: space-between;
				}
				.afterLogin_shop{
					display: inline-block;
					max-width: 10em;
					overflow: hidden;
					-o-text-overflow: ellipsis;
					   text-overflow: ellipsis;
					white-space: nowrap;
					font-size: .426667rem;
					line-height: 1.5em;
					color: #333;
				}
				.afterLogin_ok{
					font-size: .346667rem;
					text-align: right;
					color: #333;
					max-width: 10em;
					overflow: hidden;
					-o-text-overflow: ellipsis;
					   text-overflow: ellipsis;
					white-space: nowrap;
				}
			}
		}
		.afterLogin_body_head{
			padding-right: .266667rem;
			-ms-flex: none;
			    flex: none;
			img{
				border-radius: .053333rem;
				border:1PX solid #eee;
				width: .853333rem;
				height: .853333rem;
				max-width: 100%;
			}
		}
	}
}


