.active_itemAll{
	width: 560px;
	-ms-flex-align: center;
	    align-items: center;
}
.active_item{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	line-height: 36px;
	font-size: 20px;
	span{
		-ms-flex:1 1;
		    flex:1 1;
		overflow: hidden;
		white-space: nowrap;
		-o-text-overflow: ellipsis;
		   text-overflow: ellipsis;
	}
	.active_logo{
		display: -ms-flexbox;
		display: flex;
		width: 28px;
		height: 28px;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: center;
		    justify-content: center;
		font-size: 20px;
		margin-right: 12px;
		font-style: normal;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		text-align: center;
		border-radius: 4px;
	}
}
.show_active{
	display: -ms-flexbox;
	display: flex;
	position: relative;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	.active_item:first-child{
		padding-right:140px;
	}
	.active_item:not(:first-child){
		margin-top: 2.5px;
	}
	.show_more{
		position: absolute;
		right: 0;
		top: 0;
		padding: 3px 20px 7.5px 0;
		height: 100%;
		color: #999;
		text-align: right;
		font-size: 20px;
		line-height: 1;
		span{
			display: inline-block;
			-webkit-transform: scale(0.85);
			    -ms-transform: scale(0.85);
			        transform: scale(0.85);
		}
		.show_num{
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			    align-items: center;
		}
		svg{
			width: .173333rem;
			height: .173333rem;
			opacity: .9;
			-webkit-transition:all .3s ease-in-out;
			-o-transition: all .3s ease-in-out;
			transition: all .3s ease-in-out;
			-webkit-transform:rotate(0deg);
			-ms-transform: rotate(0deg);
			    transform: rotate(0deg);
			fill:currentColor;
			will-change:transform;
			transition: all .3s ease-in-out;
		}
		.nowSvg{
			-webkit-transform: rotate(180deg);
			    -ms-transform: rotate(180deg);
			        transform: rotate(180deg);
		}
	}
	.on{
		&::after{
			-webkit-transform:rotate(-180deg);
			    -ms-transform:rotate(-180deg);
			        transform:rotate(-180deg);
		}
	}
	.active_num{
		display: none;
	}
}


