.index_header{
	padding:20px 20px 0;
	color: #fff;
	background-image: -webkit-gradient(linear,left top, right top,from(#0af),to(#0085ff));
	background-image: -webkit-linear-gradient(left,#0af,#0085ff);
	background-image: -o-linear-gradient(left,#0af,#0085ff);
	background-image: linear-gradient(90deg,#0af,#0085ff);
	.header_title{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: justify;
		    justify-content: space-between;
		height: 69px;
		-ms-flex-align: center;
		    align-items: center;
	}
}
.index-address{
	width:60%;
	font-weight: 700;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	.address_icon{
		width: 30px;
		height: 35px;
		background:url('./address.png') no-repeat center;
		background-size: cover;
	}
	.sj{
		border-style: solid;
		border-width: 6px;
		border-color: transparent;
		border-top-color: #fff;
	}
}
.index-word{
	margin:0 10px;
	font-size: .48rem;
	max-width: 80%;
	white-space: nowrap;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}
.weather{
	display: -ms-flexbox;
	display: flex;
	text-align: right;
	-ms-flex-align: center;
	    align-items: center;
	.weather_today{
		font-size: 24px;
		-webkit-transform: scale(0.8);
		    -ms-transform: scale(0.8);
		        transform: scale(0.8);
	}
	.weather_num{
		font-size: 28px;
	}
	.weather-icon{
		margin-left: 5px;
		width:55px;
		height: 55px;
	}
}


