.index_footer{
	height: 120px;
	.index_footer_con{
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 999;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: justify;
		    justify-content: space-between;
		background: #fff;
		height: 100px;
		-webkit-box-shadow: 0 -1PX 1PX rgba(0,0,0,0.1);
		        box-shadow: 0 -1PX 1PX rgba(0,0,0,0.1);
		font-size: 0;
	}
	.index_footer_a{
		-ms-flex: 1 1;
		    flex: 1 1;
		text-align: center;
		a{
			display: block;
			width: 100%;
			height: 100%;
		}
	}
	.index_footer_icon{
		width: 40px;
		height: 40px;
	}
	.index_footer_word{
		display: block;
		font-size: 24px;
		color: #666;
		margin-top: 1px;
		-webkit-transform: scale(.9);
		    -ms-transform: scale(.9);
		        transform: scale(.9);
	}
	.active g,.active path,.active .index_footer_word{
		stroke: #2295FF!important;
		color: #2295FF;
	}
	.active .first path{
		fill: #2295FF!important;
	}
}


