.find_active{
	display: -ms-flexbox;
	display: flex;
	background: #fff;
	overflow: hidden;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	& .everyActivity:nth-child(odd){
		border-right: 1PX solid #ededed;
	}
	.everyActivity{
		display: -ms-flexbox;
		display: flex;
		width: 50%;
		height: 160px;
		padding: 0 30px;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: justify;
		    justify-content: space-between;
		border-bottom: 1PX solid #ededed;
		.icon{
			width: 1.2rem;
			max-width: 100%;
		}
		.content_wrapper{
			max-width: 3rem;
			p{
				line-height: 1.2;
				font-size: .426667rem;
				color: #999;
				overflow: hidden;
				white-space: nowrap;
				-o-text-overflow: ellipsis;
				   text-overflow: ellipsis;
			}
			.tips1{
				margin-bottom: .16rem;
			}
			.tips2{
				font-size: .32rem;
			}
		}
	}
}
.list2{
	.sub-pic-hash{
		height: 180px;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: center;
		    justify-content: center;
	}
	img{
		width: 100%;
	}
}
.parts .find_active:first-child{
	margin-bottom: .28rem;
}
section.findList{
	margin-bottom: .28rem;
	border-top: 1PX solid #eee;
	background-color: #fff;
}


